'use client';

import { cn } from '@/lib/utils';
import { STATUS_CONFIG, VERTICALS, VERTICAL_COLORS } from '@/lib/constants';
import type { ContentStatus, Vertical } from '@/lib/types';

interface StatusBadgeProps {
  status: ContentStatus;
  className?: string;
}

export function StatusBadge({ status, className }: StatusBadgeProps) {
  const config = STATUS_CONFIG[status];

  return (
    <span className={cn('status-badge', config.className, className)}>
      <span className={cn('w-1.5 h-1.5 rounded-full', config.dotColor)} />
      {config.label}
    </span>
  );
}

interface VerticalBadgeProps {
  vertical: Vertical;
  showLabel?: boolean;
  className?: string;
}

export function VerticalBadge({
  vertical,
  showLabel = true,
  className,
}: VerticalBadgeProps) {
  const config = VERTICALS[vertical];
  const color = VERTICAL_COLORS[vertical];

  return (
    <span
      className={cn(
        'vertical-badge',
        config.bgColor,
        config.borderColor,
        'border',
        className
      )}
    >
      <span
        className="vertical-dot"
        style={{ backgroundColor: color }}
      />
      {showLabel && (
        <span style={{ color }}>{config.abbreviation}</span>
      )}
    </span>
  );
}

interface CountBadgeProps {
  count: number;
  className?: string;
}

export function CountBadge({ count, className }: CountBadgeProps) {
  if (count === 0) return null;

  return (
    <span
      className={cn(
        'inline-flex items-center justify-center min-w-[20px] h-5 px-1.5 rounded-full',
        'bg-tsi-blue text-white text-xs font-medium',
        className
      )}
    >
      {count > 99 ? '99+' : count}
    </span>
  );
}
