'use client';

import { useRouter, useSearchParams } from 'next/navigation';
import { useState, useEffect, useCallback } from 'react';
import { motion } from 'framer-motion';
import { Search, X, Filter, FileText, Mail, Linkedin, CheckCircle2 } from 'lucide-react';
import { GlassPanel } from '@/components/ui/GlassPanel';
import { STATUS_CONFIG, CONTENT_TYPE_LABELS } from '@/lib/constants';
import type { ContentType, ContentStatus, VaultFilters as VaultFiltersType } from '@/lib/types';

interface VaultFiltersProps {
  filters: Partial<VaultFiltersType>;
  totalCount: number;
}

const contentTypes: { type: ContentType; icon: typeof Mail }[] = [
  { type: 'Email', icon: Mail },
  { type: 'LinkedIn', icon: Linkedin },
  { type: 'Brochure', icon: FileText },
];

const statuses: ContentStatus[] = ['DRAFT', 'APPROVED', 'PUBLISHED', 'ARCHIVED'];

export function VaultFilters({ filters, totalCount }: VaultFiltersProps) {
  const router = useRouter();
  const searchParams = useSearchParams();
  const [searchValue, setSearchValue] = useState(filters.search || '');

  const updateParam = useCallback((key: string, value: string | null) => {
    const newParams = new URLSearchParams(searchParams.toString());
    if (value) {
      newParams.set(key, value);
    } else {
      newParams.delete(key);
    }
    router.push(`?${newParams.toString()}`, { scroll: false });
  }, [router, searchParams]);

  // Debounced search
  useEffect(() => {
    const timer = setTimeout(() => {
      updateParam('search', searchValue || null);
    }, 300);
    return () => clearTimeout(timer);
  }, [searchValue, updateParam]);

  const toggleArrayParam = useCallback((key: string, value: string) => {
    const current = searchParams.get(key)?.split(',').filter(Boolean) || [];
    const newValues = current.includes(value)
      ? current.filter((v) => v !== value)
      : [...current, value];

    updateParam(key, newValues.length > 0 ? newValues.join(',') : null);
  }, [searchParams, updateParam]);

  const clearFilters = () => {
    router.push('/vault', { scroll: false });
    setSearchValue('');
  };

  const hasFilters =
    filters.search ||
    filters.contentTypes?.length ||
    filters.statuses?.length;

  return (
    <GlassPanel className="p-5">
      <div className="flex flex-col lg:flex-row gap-4">
        {/* Search Input */}
        <div className="flex-1 relative">
          <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-white/40" />
          <input
            type="text"
            placeholder="Search assets..."
            value={searchValue}
            onChange={(e) => setSearchValue(e.target.value)}
            className="input-field pl-10 pr-10"
          />
          {searchValue && (
            <button
              onClick={() => setSearchValue('')}
              className="absolute right-3 top-1/2 -translate-y-1/2 p-1 rounded hover:bg-white/10"
            >
              <X className="w-4 h-4 text-white/40" />
            </button>
          )}
        </div>

        {/* Content Type Filters */}
        <div className="flex items-center gap-2">
          <span className="text-xs text-white/50 uppercase tracking-wider">Type</span>
          <div className="flex gap-1">
            {contentTypes.map(({ type, icon: Icon }) => {
              const isActive = filters.contentTypes?.includes(type);
              return (
                <motion.button
                  key={type}
                  onClick={() => toggleArrayParam('types', type)}
                  className={`p-2 rounded-lg border transition-colors ${
                    isActive
                      ? 'bg-tsi-blue/20 border-tsi-blue/50 text-tsi-blue-light'
                      : 'border-white/10 text-white/60 hover:bg-white/5 hover:text-white'
                  }`}
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                  title={CONTENT_TYPE_LABELS[type]}
                >
                  <Icon className="w-4 h-4" />
                </motion.button>
              );
            })}
          </div>
        </div>

        {/* Status Filters */}
        <div className="flex items-center gap-2">
          <span className="text-xs text-white/50 uppercase tracking-wider">Status</span>
          <div className="flex gap-1">
            {statuses.map((status) => {
              const isActive = filters.statuses?.includes(status);
              const config = STATUS_CONFIG[status];
              return (
                <motion.button
                  key={status}
                  onClick={() => toggleArrayParam('statuses', status)}
                  className={`px-3 py-1.5 rounded-lg border text-xs font-medium transition-colors ${
                    isActive
                      ? config.className
                      : 'border-white/10 text-white/60 hover:bg-white/5 hover:text-white'
                  }`}
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                >
                  {config.label}
                </motion.button>
              );
            })}
          </div>
        </div>
      </div>

      {/* Results Count & Clear */}
      <div className="flex items-center justify-between mt-4 pt-4 border-t border-white/10">
        <div className="flex items-center gap-2 text-sm text-white/60">
          <Filter className="w-4 h-4" />
          <span>
            <span className="text-white font-medium">{totalCount}</span> assets found
          </span>
        </div>
        {hasFilters && (
          <button
            onClick={clearFilters}
            className="text-sm text-tsi-blue-light hover:text-white transition-colors flex items-center gap-1"
          >
            <X className="w-4 h-4" />
            Clear filters
          </button>
        )}
      </div>
    </GlassPanel>
  );
}
