'use client';

import { useState } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import { motion, AnimatePresence } from 'framer-motion';
import {
  X,
  Copy,
  Check,
  Download,
  Calendar,
  User,
  Target,
  FileJson,
  FileText,
  Mail,
  Linkedin,
  BookOpen,
  ScrollText,
  PenLine,
  Send,
  Newspaper,
  Globe,
  File,
  Package,
  ShieldCheck,
} from 'lucide-react';
import { VerticalBadge, StatusBadge } from '@/components/ui/Badge';
import { VERTICAL_COLORS, CONTENT_TYPE_LABELS } from '@/lib/constants';
import { format } from 'date-fns';
import type { Asset, ContentType } from '@/lib/types';

interface AssetSheetProps {
  asset: Asset;
}

const contentTypeIcons: Record<ContentType, typeof Mail> = {
  Email: Mail,
  LinkedIn: Linkedin,
  Brochure: FileText,
  CaseStudy: BookOpen,
  Whitepaper: ScrollText,
  BlogPost: PenLine,
  SalesEmail: Send,
  Newsletter: Newspaper,
  WebPage: Globe,
  ContentPackage: Package,
  ComplianceReview: ShieldCheck,
  Unknown: File,
};

export function AssetSheet({ asset }: AssetSheetProps) {
  const router = useRouter();
  const searchParams = useSearchParams();
  const [copied, setCopied] = useState(false);

  const Icon = contentTypeIcons[asset.contentType];
  const color = VERTICAL_COLORS[asset.vertical];

  const handleClose = () => {
    const newParams = new URLSearchParams(searchParams.toString());
    newParams.delete('asset');
    router.push(`?${newParams.toString()}`, { scroll: false });
  };

  const handleCopy = async () => {
    await navigator.clipboard.writeText(asset.rawMarkdown);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  const handleDownloadJson = () => {
    const data = {
      title: asset.title,
      vertical: asset.vertical,
      contentType: asset.contentType,
      status: asset.status,
      date: asset.date,
      frontmatter: asset.frontmatter,
      content: asset.rawMarkdown,
    };
    downloadFile(JSON.stringify(data, null, 2), `${asset.slug}.json`, 'application/json');
  };

  const handleDownloadMarkdown = () => {
    downloadFile(asset.rawMarkdown, `${asset.slug}.md`, 'text/markdown');
  };

  return (
    <AnimatePresence>
      {/* Backdrop */}
      <motion.div
        className="sheet-overlay"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
        onClick={handleClose}
      />

      {/* Sheet */}
      <motion.div
        className="sheet-content"
        initial={{ x: '100%' }}
        animate={{ x: 0 }}
        exit={{ x: '100%' }}
        transition={{ type: 'spring', damping: 25, stiffness: 200 }}
      >
        {/* Header */}
        <div className="sticky top-0 z-10 bg-tsi-navy-light border-b border-white/10 p-6">
          <div className="flex items-start justify-between gap-4">
            <div className="flex items-start gap-4">
              <div
                className="w-14 h-14 rounded-xl flex items-center justify-center flex-shrink-0"
                style={{ backgroundColor: `${color}20` }}
              >
                <Icon className="w-7 h-7" style={{ color }} />
              </div>
              <div>
                <p className="text-sm text-white/50">
                  {CONTENT_TYPE_LABELS[asset.contentType]}
                </p>
                <h2 className="text-xl font-semibold text-white mt-1">
                  {asset.title}
                </h2>
                <div className="flex items-center gap-2 mt-2">
                  <VerticalBadge vertical={asset.vertical} />
                  <StatusBadge status={asset.status} />
                </div>
              </div>
            </div>
            <button
              onClick={handleClose}
              className="p-2 rounded-lg hover:bg-white/10 transition-colors"
            >
              <X className="w-5 h-5 text-white/60" />
            </button>
          </div>

          {/* Quick Actions */}
          <div className="flex items-center gap-2 mt-4">
            <button
              onClick={handleCopy}
              className="btn-secondary text-sm flex-1"
            >
              {copied ? (
                <>
                  <Check className="w-4 h-4 text-green-400" />
                  Copied!
                </>
              ) : (
                <>
                  <Copy className="w-4 h-4" />
                  Copy Content
                </>
              )}
            </button>
            <button
              onClick={handleDownloadJson}
              className="btn-ghost text-sm"
              title="Download JSON"
            >
              <FileJson className="w-4 h-4" />
            </button>
            <button
              onClick={handleDownloadMarkdown}
              className="btn-ghost text-sm"
              title="Download Markdown"
            >
              <Download className="w-4 h-4" />
            </button>
          </div>
        </div>

        {/* Content */}
        <div className="p-6 space-y-6">
          {/* Metadata */}
          <div className="grid grid-cols-2 gap-4">
            <MetadataItem
              icon={Calendar}
              label="Date"
              value={formatDate(asset.date)}
            />
            {asset.frontmatter.author && (
              <MetadataItem
                icon={User}
                label="Author"
                value={asset.frontmatter.author}
              />
            )}
            {asset.frontmatter.campaign && (
              <MetadataItem
                icon={Target}
                label="Campaign"
                value={asset.frontmatter.campaign}
              />
            )}
            {asset.frontmatter.target_audience && (
              <MetadataItem
                icon={User}
                label="Audience"
                value={asset.frontmatter.target_audience}
              />
            )}
          </div>

          {/* Proof Points */}
          {asset.frontmatter.proof_points && asset.frontmatter.proof_points.length > 0 && (
            <div>
              <h3 className="text-sm font-medium text-white/70 mb-2">Proof Points</h3>
              <div className="flex flex-wrap gap-2">
                {asset.frontmatter.proof_points.map((point, index) => (
                  <span
                    key={index}
                    className="px-2 py-1 bg-white/10 rounded text-xs text-white/80"
                  >
                    {point}
                  </span>
                ))}
              </div>
            </div>
          )}

          {/* Content Preview */}
          <div>
            <h3 className="text-sm font-medium text-white/70 mb-3">Content</h3>
            <div
              className="prose-dark bg-black/20 rounded-xl p-6 border border-white/10"
              dangerouslySetInnerHTML={{ __html: asset.content }}
            />
          </div>

          {/* Raw Frontmatter */}
          <div>
            <h3 className="text-sm font-medium text-white/70 mb-3">Frontmatter</h3>
            <pre className="bg-black/30 rounded-xl p-4 border border-white/10 overflow-x-auto text-xs text-white/70">
              {JSON.stringify(asset.frontmatter, null, 2)}
            </pre>
          </div>
        </div>
      </motion.div>
    </AnimatePresence>
  );
}

function MetadataItem({
  icon: Icon,
  label,
  value,
}: {
  icon: typeof Calendar;
  label: string;
  value: string;
}) {
  return (
    <div className="flex items-center gap-3 p-3 bg-white/5 rounded-lg">
      <Icon className="w-4 h-4 text-white/40" />
      <div>
        <p className="text-xs text-white/50">{label}</p>
        <p className="text-sm text-white">{value}</p>
      </div>
    </div>
  );
}

function formatDate(dateStr: string): string {
  try {
    return format(new Date(dateStr), 'MMMM d, yyyy');
  } catch {
    return dateStr;
  }
}

function downloadFile(content: string, filename: string, mimeType: string) {
  const blob = new Blob([content], { type: mimeType });
  const url = URL.createObjectURL(blob);
  const link = document.createElement('a');
  link.href = url;
  link.download = filename;
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  URL.revokeObjectURL(url);
}
