'use client';

import { motion, AnimatePresence } from 'framer-motion';
import { Library } from 'lucide-react';
import { AssetCard } from './AssetCard';
import type { AssetSummary } from '@/lib/types';

interface AssetGalleryProps {
  assets: AssetSummary[];
}

const containerVariants = {
  hidden: { opacity: 0 },
  show: {
    opacity: 1,
    transition: {
      staggerChildren: 0.05,
    },
  },
};

export function AssetGallery({ assets }: AssetGalleryProps) {
  if (assets.length === 0) {
    return (
      <div className="flex flex-col items-center justify-center py-20">
        <Library className="w-16 h-16 text-white/20 mb-4" />
        <h2 className="text-xl font-semibold text-white/60">No assets found</h2>
        <p className="text-white/40 mt-1">
          Try adjusting your filters or search query
        </p>
      </div>
    );
  }

  return (
    <motion.div
      className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4"
      variants={containerVariants}
      initial="hidden"
      animate="show"
    >
      <AnimatePresence mode="popLayout">
        {assets.map((asset) => (
          <AssetCard key={asset.slug} asset={asset} />
        ))}
      </AnimatePresence>
    </motion.div>
  );
}
