'use client';

import { useRouter, useSearchParams } from 'next/navigation';
import { motion } from 'framer-motion';
import {
  Mail,
  Linkedin,
  FileText,
  BookOpen,
  ScrollText,
  PenLine,
  Send,
  Newspaper,
  Globe,
  File,
  Eye,
  Calendar,
  Package,
  ShieldCheck,
} from 'lucide-react';
import { GlassPanel } from '@/components/ui/GlassPanel';
import { VerticalBadge, StatusBadge } from '@/components/ui/Badge';
import { VERTICAL_COLORS, CONTENT_TYPE_LABELS } from '@/lib/constants';
import { format } from 'date-fns';
import type { AssetSummary, ContentType } from '@/lib/types';

interface AssetCardProps {
  asset: AssetSummary;
}

const contentTypeIcons: Record<ContentType, typeof Mail> = {
  Email: Mail,
  LinkedIn: Linkedin,
  Brochure: FileText,
  CaseStudy: BookOpen,
  Whitepaper: ScrollText,
  BlogPost: PenLine,
  SalesEmail: Send,
  Newsletter: Newspaper,
  WebPage: Globe,
  ContentPackage: Package,
  ComplianceReview: ShieldCheck,
  Unknown: File,
};

const itemVariants = {
  hidden: { opacity: 0, scale: 0.95 },
  show: { opacity: 1, scale: 1 },
  exit: { opacity: 0, scale: 0.95 },
};

export function AssetCard({ asset }: AssetCardProps) {
  const router = useRouter();
  const searchParams = useSearchParams();

  const Icon = contentTypeIcons[asset.contentType];
  const color = VERTICAL_COLORS[asset.vertical];

  const handleClick = () => {
    const newParams = new URLSearchParams(searchParams.toString());
    newParams.set('asset', asset.slug);
    router.push(`?${newParams.toString()}`, { scroll: false });
  };

  return (
    <motion.div
      variants={itemVariants}
      layout
      layoutId={asset.slug}
    >
      <GlassPanel
        hover
        className="p-5 cursor-pointer group"
        onClick={handleClick}
      >
        <div className="flex items-start gap-4">
          {/* Icon */}
          <div
            className="w-12 h-12 rounded-xl flex items-center justify-center flex-shrink-0 transition-transform group-hover:scale-110"
            style={{ backgroundColor: `${color}20` }}
          >
            <Icon className="w-6 h-6" style={{ color }} />
          </div>

          {/* Content */}
          <div className="flex-1 min-w-0">
            {/* Type Label */}
            <p className="text-xs text-white/50 mb-1">
              {CONTENT_TYPE_LABELS[asset.contentType]}
            </p>

            {/* Title */}
            <h3 className="text-base font-semibold text-white line-clamp-2 group-hover:text-tsi-blue-light transition-colors">
              {asset.title}
            </h3>

            {/* Date */}
            <p className="text-xs text-white/40 mt-1 flex items-center gap-1">
              <Calendar className="w-3 h-3" />
              {formatDate(asset.date)}
            </p>

            {/* Badges */}
            <div className="flex items-center gap-2 mt-3">
              <VerticalBadge vertical={asset.vertical} />
              <StatusBadge status={asset.status} />
            </div>
          </div>
        </div>

        {/* Hover Overlay */}
        <div className="absolute inset-0 flex items-center justify-center bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity rounded-2xl">
          <div className="flex items-center gap-2 text-white font-medium">
            <Eye className="w-5 h-5" />
            View Asset
          </div>
        </div>
      </GlassPanel>
    </motion.div>
  );
}

function formatDate(dateStr: string): string {
  try {
    return format(new Date(dateStr), 'MMM d, yyyy');
  } catch {
    return dateStr;
  }
}
